.op-files-tab
  &--file-upload
    margin-top: $spot-spacing-0-75

  &--file-list
    margin-bottom: $spot-spacing-0-75

    &-item
      &-action
        text-decoration: none

        &.view-not-allowed
          opacity: 0.5

        &.disabled
          color: $spot-color-basic-gray-3
          opacity: 0.5
          pointer-events: none

          .op-files-tab--icon
            color: $spot-color-basic-gray-3

        &:hover
          .op-files-tab--file-list-item-title
            text-decoration: underline

      &-title
        @include spot-body-small

        line-height: $spot-spacing-1-5
        word-break: normal
        @include text-shortener
        padding-right: $spot_spacing-0-5

      &-text
        @include spot-caption()

        line-height: 1.5rem
        color: $spot-color-basic-gray-3
        flex-shrink: 0

        &:not(:last-child)
          margin-right: $spot-spacing-0-5

      &-avatar
        width: $spot-spacing-1-5
        height: $spot-spacing-1-5
        display: inline-block

      &-floating-wrapper:not(&-floating-wrapper__disabled):hover &-text,
      &-floating-wrapper:not(&-floating-wrapper__disabled):hover &-avatar
        visibility: hidden

      &-floating-actions
        padding-right: 0

      &-floating-text
        @include spot-body-small()

        padding-right: $spot-spacing-0-25
        overflow: hidden
        color: var(--primary-color)

        &-icon
          margin-right: $spot-spacing-0-5

  &--file-list &--file-list-item &--file-list-item-action
    padding-left: 0
    padding-right: 0

  &--storage-info-box
    margin-top: 0.875rem
    display: grid
    align-items: center
    grid-template: "icon text" "button button" / auto 1fr

    &-icon
      color: #9A9A9A
      grid-area: icon
      width: 3.375rem
      height: 3.375rem
      font-size: 1.75rem
      margin-right: $spot_spacing-0-5

    .text-box
      grid-area: text

      .text-box-header
        font-weight: 700
        line-height: $spot-spacing-1-5

      .text-box-content
        @include spot-body-small()

        color: #9A9A9A

    .button-box
      grid-area: button
      display: flex
      justify-content: end

    &-button
      margin: $spot_spacing-0-75 0 0

      &:not(:last-child)
        margin-right: $spot-spacing-0-5

  &--icon
    width: $spot-spacing-1-5
    height: $spot-spacing-1-5

    &_pdf
      color: #B93A33

    &_img
      color: #0081C7

    &_mov
      color: #7C006E

    &_txt
      color: #9A9A9A

    &_doc
      color: #006E8F

    &_sheet
      color: #007528

    &_presentation
      color: #EF9E56

    &_form
      color: #87E2C1

    &_dir
      color: #00BDF3

    &_default
      color: #9A9A9A

    &_clip
      color: #333333

    &_nextcloud
      color: #1A67A3
